/* -----------------BASICS COLORS & FONTS VARIABLES---------------*/

:root {
  /**********Colors************/
  --first-color: hsl(334, 83%, 58%);
  --title-color: hsl(240, 6%, 17%);
  --text-color: hsl(247, 4%, 50%);
  --body-color: hsl(320, 60%, 98%);
  --container-color: #fff;

  /************Font Size***********/
  --body-font: "Alkatra", cursive;
  --h1-font-size: 2.25rem;
  --h2-font-size: 1.5rem;
  --h3-font-size: 1.25rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.9rem;
  --smaller-font-size: 0.8rem;

  /************Font Weight***********/
  --font-medium: 500;
  --font-semibold: 600;
  --font-bold: 700;

  /************ Box Shadow ***********/

  --shadow: 0px 5px 20px 0px rgb(96 67 96 / 10%);

  /************ Border Radius ***********/
  --border-radius: 20px;
}

/* ----------------- Responsive --------------*/

@media screen and (max-width: 1024px) {
  :root {
    --h1-font-size: 1.75rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1rem;
    --normal-font-size: 0.938rem;
    --small-font-size: 0.813rem;
    --smaller-font-size: 0.75rem;
  }
}

/* ----------------- BASE --------------*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body,
button,
input,
textarea {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
}

body {
  background-color: var(--body-color);
  line-height: 1.7;
  color: var(--text-color);
}

h1,
h2,
h3 {
  color: var(--title-color);
  font-weight: var(--font-semibold);
  line-height: 1.2;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

/* ----------------- Reusable CSS Classes --------------*/

.container {
  max-width: 1080px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

.grid {
  display: grid;
}

.section {
  padding-top: 7rem;
  padding-bottom: 2rem;
}

.section__title {
  position: relative;
  margin-left: 0.9rem;
  margin-bottom: 3.75rem;
  font-size: var(--h1-font-size);
  font-weight: var(--font-bold);
}

.section__title::before {
  content: "";
  height: 2.25rem;
  width: 2.25rem;
  position: absolute;
  left: -0.9rem;
  top: -0.9rem;
  /* background-image: url(./assets/bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed; */
}
.btn {
  padding: 0.75rem;
  width: 150px;
  line-height: 1;
  border-radius: 1.9rem;
  box-shadow: 0 0 1px rgb(0 0 0 / 0%);
  border: 1px solid transparent;
  color: #fff;
  display: inline-block;
  background-color: var(--first-color);
  font-weight: var(--font-bold);
}

@keyframes button-push {
  50% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}
.btn:hover {
  animation: button-push 0, 3s linear 1;
}
